@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@700&family=Montserrat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@700&display=swap);
* {
  font-family: "Montserrat", sans-serif;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  width: 100vw;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#alert {
  position: absolute;
  top: 1%;
  left: 50%;
  transform: translateX(-50%);
  width: 40vw;
  z-index: 9999;
}

/* antd css */
.ant-carousel .slick-dots li button {
  background: gray !important;
  opacity: 0.5 !important;
}

.ant-carousel .slick-dots li.slick-active button {
  background: red !important;
}

.ant-carousel .slick-dots-bottom {
  bottom: -20px !important;
}

.ant-card-body {
  padding: 0 !important;
}

.BrainhubCarousel__dots .BrainhubCarousel__dot:before {
  width: 8px !important;
  height: 8px !important;
}

.BrainhubCarousel__dots
  .BrainhubCarousel__dot.BrainhubCarousel__dot--selected:before {
  background: red !important;
}

button.BrainhubCarousel__dot {
  background: none !important;
}

.BrainhubCarousel__thumbnail {
  padding: 5px !important;
}

.ant-modal-body {
  padding-top: 0 !important;
}

header.ant-layout-header {
  height: auto !important;
}

.ant-collapse-content > .ant-collapse-content-box {
  padding: 0 !important;
}

#body {
  margin-top: 10px;
  padding: 0 20%;
}

#logo {
  font-weight: bold;
  color: black;
  font-family: 'Josefin Sans', sans-serif;
  display: inline-block;
}

Header {
  display: flex;
  align-items: center; 
  justify-content: space-evenly;
  flex-wrap: wrap;
  text-align: center;
  border-bottom: 1px solid #ccc;
}

#image-wrapper {
  margin-bottom: 20px;
  overflow: Hidden;
  width: 300px;
  height: 200px;
  border-radius: 2%;
  display: inline-block; /* FF3.6+ */ /* Chrome,Safari4+ */ /* Chrome10+,Safari5.1+ */ /* Opera 11.10+ */ /* IE10+ */
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.65) 100%
  ); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6000000', endColorstr='#00000000',GradientType=0 ); /* IE6-9 */
}

#image-wrapper:hover img {
  transform: scale(1.25);
}

#image-wrapper img {
  width: 100%;
  height: 100%;
  transition: transform 500ms;
  position: relative;
  z-index: -1;
  display: block;
}

#category {
  position: absolute;
  top: 0;
  z-index: 99;
  color: red;
  transform: translate(-50%, -50%);
  top: -100px;
  left: 50%;
  background: white;
  padding: 3px 8px;
  font-family: 'Mulish', sans-serif;
  font-weight: 700;
}

#images-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

#wrapper {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 100px;
  color: white;
}

.dotStyle {
  background: green;
  color: red;
}

#image {
  width: 240px;
  height: 300px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  padding: 0 100px;
}

